* {
  box-sizing: border-box;
}

*::after,
::before {
  box-sizing: inherit;
}

html {
  font-family: "TTFirsNeue", Arial, Helvetica, sans-serif;
}

img {
  width: 100%;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

a {
  text-decoration: none;
}

picture {
  font-size: 0;
}

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 15px;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.header.fixed {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 100;
  background-color: rgba(32, 32, 32, 0.9);
}

.close {
  display: none;
}

.header {
  background-color: #202020;
}
.header.fixed .nav-list__item:not(:last-child) {
  margin-right: 40px;
}
.header.fixed .nav-list {
  margin-right: 30px;
}
.header.fixed .social {
  display: none;
}

.header-container {
  padding-top: 24px;
  padding-bottom: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.header-container .nav-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.header-container .nav-list__item {
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
}
.header-container .nav-list__item:not(:last-child) {
  margin-right: 79px;
}
.header-container .nav-list__link {
  padding: 0 1px;
  color: #fff;
  transition: color 0.15s linear;
}
.header-container .social__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.header-container .social__item:not(:last-child) {
  margin-right: 20px;
}
.header-container__btn {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  cursor: pointer;
  display: none;
}

.hero-container {
  padding-top: 41px;
  padding-bottom: 38px;
  display: grid;
  grid-template-rows: minmax(10px, 25px) minmax(186px, 300px) minmax(105px, 257px);
  grid-template-columns: repeat(4, minmax(124px, 300px));
  grid-auto-rows: 300px;
  gap: 25px 30px;
  grid-template-areas: "camera camera text text" "camera camera model1 model1" "camera camera model2 model3";
}
.hero-container__content {
  position: relative;
  padding: 405px 145px 60px 60px;
  grid-area: camera;
  background-image: url(../img/hero/desktop/camera.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
}
.hero-container__content .hero-content__author {
  margin-bottom: 3px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
}
.hero-container__content .hero-content__time {
  margin-bottom: 19px;
  font-weight: 700;
  font-size: 36px;
  line-height: 104%;
}
.hero-container__content .hero-content__btn {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  cursor: pointer;
  padding: 15.5px 22px;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  background-color: #FF6E30;
  outline: 3px solid transparent;
  transition: outline 0.15s linear;
}
.hero-container__content .notify-hashtag {
  padding: 6px 14px;
  position: absolute;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  background: #333;
  top: 15px;
  right: 15px;
}
.hero-container__title {
  grid-area: text;
  font-weight: 700;
  font-size: 24px;
  line-height: 104%;
}
.hero-container__img-1 {
  grid-area: model1;
}
.hero-container__img-2 {
  grid-area: model2;
}
.hero-container__img-3 {
  grid-area: model3;
}

.about {
  background-color: #202020;
}
.about .about-container {
  padding-top: 30px;
  padding-bottom: 40px;
}
.about .about-container__title {
  color: #fff;
  font-weight: 700;
  font-size: 24px;
  line-height: 31px;
  opacity: 0;
  transform: translateX(-50px);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.about .about-container__title.animation-class {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.about .about-content {
  display: grid;
  grid-template-columns: repeat(2, minmax(342px, 630px));
  gap: 25px 30px;
  grid-template-areas: "text photo" "text form";
}
.about .about-content__descr {
  grid-area: text;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  color: #cacaca;
  opacity: 0;
  transform: translateX(-50px);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.about .about-content__img {
  grid-area: photo;
}
.about .about-content__descr.animation-class {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.about .about-content__img img {
  opacity: 0;
  transform: scale(0.5);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.about .about-content__img.animation-class img {
  opacity: 1;
  transform: scale(1);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.about .about-content .form {
  grid-area: form;
  opacity: 0;
  transform: translateX(50px);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.about .about-content .form__title {
  display: block;
  margin-bottom: 16px;
  color: #fff;
  font-weight: 400;
  font-size: 24px;
  line-height: 22px;
}
.about .about-content .form__input {
  margin-right: 26px;
  margin-bottom: 20px;
  max-width: 474px;
  width: 100%;
  padding: 15px 0 16px 23px;
  color: #fff;
  background-color: #4a4a4a;
  border: none;
  outline: 1px solid transparent;
}
.about .about-content .form__input::placeholder {
  color: #cacaca;
}
.about .about-content .form__btn {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  cursor: pointer;
  padding: 15.5px 22px;
  font-family: "Roboto";
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #fff;
  background-color: #FF6E30;
  outline: 3px solid transparent;
  transition: outline 0.15s linear;
}
.about .about-content .form.animation-class {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.projects .projects-container {
