}
.projects .grid .grid-article {
  position: relative;
  padding: 22px 0 30px 30px;
  background-repeat: no-repeat;
  background-size: cover;
}
.projects .grid .grid-article__content {
  max-width: 393px;
  color: #fff;
}
.projects .grid .grid-article__date {
  margin-bottom: 2px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 12px;
  line-height: 22px;
}
.projects .grid .grid-article__title {
  margin-bottom: 6px;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
}
.projects .grid .grid-article__descr {
  margin-bottom: 54px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}
.projects .grid .grid-article__btn {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  cursor: pointer;
  padding: 15.5px 22px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  outline: 3px solid #ff6e30;
  color: #fff;
}
.projects .grid .grid-article .notify-hashtag {
  padding: 6px 14px;
  position: absolute;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  background: #333;
  top: 7px;
  right: 7px;
}
.projects .grid .grid-card2 {
  background-image: url(../img/projects/desktop/projects-img4.jpg);
}
.projects .grid .grid-card3 {
  background-image: url(../img/projects/desktop/projects-img5.jpg);
}
.projects .grid .grid-card4 .grid-list {
  grid-template-columns: 1fr 1fr;
}
.projects .grid .grid-card4 .grid-list__descr {
  margin-bottom: 10px;
}
.projects .grid .grid-card4 .grid-list__date {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 12px;
  line-height: 22px;
  color: #999;
}

.contacts__title {
  margin-bottom: 22px;
  font-weight: 700;
  font-size: 24px;
  line-height: 31px;
}
.contacts .contacts-container {
  padding-bottom: 68px;
}
.contacts .contacts-container__map {
  position: relative;
  max-width: 850px;
  max-height: 460px;
  width: 100%;
}
.contacts .contacts-container .contacts-container__wrapper {
  display: grid;
  grid-template-columns: minmax(320px, 850px) minmax(429px, 410px);
  gap: 30px;
}
.contacts .contacts-container .contacts-container__wrapper .map-mask {
  padding: 175px 50px 150px 60px;
  max-width: 410px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(32, 32, 32, 0.85);
  z-index: 1;
}
.contacts .contacts-container .contacts-container__wrapper .map-mask__btn {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}
.contacts .contacts-container .contacts-container__wrapper .map-mask__title {
  margin-bottom: 5px;
  font-weight: 400;
  font-size: 24px;
  line-height: 22px;
  color: #fff;
