.footer {
  background-color: #202020;
}
.footer .footer-container {
  display: flex;
  justify-content: space-between;
  padding-top: 24px;
  padding-bottom: 16px;
}
.footer .footer-container__logo {
  display: flex;
  flex-direction: column;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #e9e9e9;
}
.footer .footer-container__text {
  max-width: 361px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #cacaca;
}
.footer .footer-container__text span {
  color: #666;
}
.footer .footer-container .social {
  display: flex;
  flex-direction: column;
}
.footer .footer-container .social__title {
  margin-bottom: 8px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #cacaca;
}
.footer .footer-container .social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
}
.footer .footer-container .social__item:not(:last-child) {
  margin-right: 21px;
}

@media (min-width: 1200px) {
  .header .header-container__list .nav-list__link:hover {
    color: #FF6E30;
  }
  .header .header-container__list .nav-list__link:focus-visible {
    outline: none;
    background-color: #FF6E30;
  }
  .header .header-container__list .nav-list__link:active {
    color: #CE4307;
  }
  .header .social__item:hover .social__link svg path {
    transition: fill 0.15s linear;
    fill: #FF6E30;
  }
  .header .social__item .social__link:focus-visible {
    outline: none;
  }
  .header .social__item .social__link:focus-visible svg path {
    outline: none;
    transition: fill 0.15s linear;
    fill: #BC572B;
  }

  .hero .hero-content__btn:hover {
    transition: background-color 0.15s linear;
    background-color: #BC572B;
  }
  .hero .hero-content__btn:focus-visible {
    outline: 3px solid #CE4307;
  }
  .hero .hero-content__btn:active {
    background-color: #CE4307;
  }

  .about .form__btn:hover {
    transition: background-color 0.15s linear;
    background-color: #BC572B;
  }
  .about .form__btn:focus-visible {
    outline: 3px solid #FF6E30;
  }
  .about .form__btn:active {
    background-color: #CE4307;
  }
  .about .form__input:hover::placeholder {
    transition: color 0.15s linear;
    color: #FF6E30;
  }
  .about .form__input:focus-visible {
    outline: 1px solid #FF6E30;
  }

  .projects .grid-article__btn:hover {
    transition: background-color 0.15s linear;
    background-color: #FF6E30;
  }
  .projects .grid-article__btn:focus-visible {
    outline: 3px solid #FF6E30;
    background-color: #BC572B;
  }
  .projects .grid-article__btn:active {
    outline: 3px solid #CE4307;
    background-color: #CE4307;
  }

  .contacts .contacts-form .contacts-form__btn:hover {
    transition: background-color 0.15s linear;
    background-color: #BC572B;
  }
  .contacts .contacts-form .contacts-form__btn:focus-visible {
    outline: 3px solid #BC572B;
  }
  .contacts .contacts-form .contacts-form__btn:active {
    background-color: #CE4307;
  }
  .contacts .contacts-form .contacts-form__input:hover::placeholder,
.contacts .contacts-form .contacts-form__textarea:hover::placeholder {
    transition: color 0.15s linear;
    color: #FF6E30;
  }
  .contacts .contacts-form .contacts-form__input:focus-visible,
.contacts .contacts-form .contacts-form__textarea:focus-visible {
    outline: 1px solid #FF6E30;
  }
  .contacts .contacts-form .contacts-form__input:active,
.contacts .contacts-form .contacts-form__textarea:active {
    outline: none;
  }
  .contacts .map-mask__btn:hover svg circle {
    transition: fill 0.15s linear;
    fill: #FF6E30;
  }

  .footer .social__item:hover .social__link svg path {
    transition: fill 0.15s linear;
    fill: #FF6E30;
  }
  .footer .social__item .social__link:focus-visible {
    outline: none;
  }
  .footer .social__item .social__link:focus-visible svg path {
    outline: none;
    transition: fill 0.15s linear;
    fill: #BC572B;
  }
}
@media (max-width: 1024px) {
  .header .nav-list__item:not(:last-child) {
    margin-right: 33px;
  }
  .header .social {
    display: none;
  }
  .header .header-container__btn {
    display: block;
  }

  .hero .hero-container {
    grid-template-rows: minmax(10px, 50px) minmax(186px, 332px) minmax(105px, 186px);
  }
  .hero .hero-container__content {
    padding: 425px 9px 30px 30px;
    background-image: url(../img/hero/1024/camera.jpg);
  }
